// Page header
.page-header {
  // display: flex;
  // justify-content: space-between;
  // align-items: center;
  padding: px-to-rem(20);
  margin: 0 auto;
  max-width: $stage-max;
  
  @include bp($bp-md) {
    padding: px-to-rem(40) px-to-rem(40) 0;
